import React from 'react'
import { Link } from 'react-scroll'

const Header = () => {
  return (
    <header id='home'>
        <nav>
            <div className="logo">
                <div className="circle"></div>
                <h3>Shashank Nathe</h3>
            </div>
            <ul>
                <li><Link to='home'>
               <span>[</span> Home  <span>]</span> </Link></li>
                <li><Link to='about'><span>[</span> About <span>]</span></Link></li>
                <li><Link to='projects'> <span>[</span> Projects <span>]</span></Link></li>
                <li><Link to='contact'> <span>[</span> Contact <span>]</span></Link></li>
            </ul>
        </nav>
    </header>
  )
}

export default Header