import React, { useLayoutEffect, useRef} from 'react'
import img1 from '../Images/Dashboard.webp' 
import img2 from '../Images/Pricing.webp' 
import img3 from '../Images/HeadFirst.webp' 
import img4 from '../Images/Help.webp' 
import img5 from '../Images/Sunkart.webp' 
import img6 from '../Images/Delivery.webp' 
import img7 from '../Images/Ezio.webp' 
import img8 from '../Images/Codevision.webp' 
import img9 from '../Images/Ivision.webp' 
import dash from '../Images/Dash.webp'
import ecom from '../Images/Ecom.webp'
import movies from '../Images/Movies.webp'
import {motion} from 'framer-motion'

const Projects = () => {
  const projectRef = useRef(null)
  const title = useRef(null)
  useLayoutEffect(()=>{
    window.addEventListener('resize',()=>{
      if(window.pageYOffset>=projectRef.current.clientHeight){
        title.current.style.position = 'static'
      }
    })
  },[])

  return (
    <section className="projects" ref={projectRef} id='projects'>
      <h3 ref={title}>Projects</h3>



      <section className="mainProject">
        <motion.article className="mp"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
            <div>
          <h3>Movie Browser</h3>
          <p>Movies app build with React. <br /> Used <strong>AXIOS</strong> api library for data fetch request from theMoviesDb api.</p>
          <button><a href="https://shashanknathe.github.io/movies/" target='_blank'>Watch Live</a></button>
        </div>
        <a href="https://shashanknathe.github.io/movies/" target='_blank'>
        <img src={movies} alt="Movies browser" /></a>
        </motion.article>
        <motion.article className="mp"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
            <div>
          <h3>Dashboard</h3>
          <p>Admin dashboard to keep track of your business. <br />Used Contect api for state management</p>
          <button><a href="https://shashanknathe.github.io/dashboard-v2/" target='_blank'>Watch Live</a></button>
        </div>
        <a href="https://shashanknathe.github.io/dashboard-v2/" target='_blank'>
        <img src={dash} alt="Dashboard" /></a>
        </motion.article>
        <motion.article className="mp"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
            <div>
          <h3>Ecommerce platform</h3>
          <p>A simple ecommerce platform. <br />Made use of different hooks and context api for state management</p>
          <button><a href="https://shashanknathe.github.io/ecommerce-v2/" target='_blank'>Watch Live</a></button>
        </div>
        <a href="https://shashanknathe.github.io/ecommerce-v2/" target='_blank'>
        <img src={ecom} alt="Ecommerce" /></a>
        </motion.article>
      </section>
      
      <div className="cards">

        <a href="https://shashanknathe.github.io/Ivision/" target='_blank'>
      <motion.article className="card"
      initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
      >
          <img src={img9} alt="Sell online" />
          </motion.article>
          </a>
          <a href="https://shashanknathe.github.io/Pricing/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img2} alt="Pricing page" />
        </motion.article>
        </a>
        <a href="https://shashanknathe.github.io/Headfirst/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img3} alt="Headfirst homepage" />
        </motion.article>
        </a>
        <a href="https://shashanknathe.github.io/Headfirst-Ezio/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img7} alt="Ezio homepage" />
        </motion.article>
        </a>

        <a href="https://shashanknathe.github.io/Codevision/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img8} alt="codevision homepage" />
        </motion.article>
        </a>
        <a href="https://shashanknathe.github.io/Dashboard/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img1} alt="stock market Dashboard" />
        </motion.article>
        </a>
        <a href="https://shashanknathe.github.io/Help/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img4} alt="Help page" />
        </motion.article>
        </a>
        <a href="https://shashanknathe.github.io/Sunkart/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img5} alt="online marketplace" />
        </motion.article>
        </a>
        <a href="https://shashanknathe.github.io/Deliveryassistant/" target='_blank'>
        <motion.article className="card"
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
          <img src={img6} alt="Delivery assistant" />
        </motion.article>
        </a>
      </div>
    </section>
  )
}

export default Projects