import React, { useRef } from 'react'
import {AiFillHtml5} from 'react-icons/ai'
import {DiCss3,DiJavascript1,DiReact,DiPython,DiGit} from 'react-icons/di'
import { motion } from "framer-motion"
const About = () => {
  
  return (
    <section className="about" id='about'>
        <h3>Profile</h3>
        <motion.article
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 ,scale:0.5}}
        transition={{ duration: 0.5 }}
        >
            <h4>About me</h4>
            <p>I am a CS Graduate with an expertise in working with <span>React</span> making responsive and beautiful user friendly websites. Able to effectively self-manage during independent projects, as well as collaborate in a team setting. Experienced with the latest cutting edge development tools and procedures. </p>
        </motion.article>
        <motion.article
        initial={{ opacity: 0, scale: 0.5 }}
        whileInView={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0 , sacle:0.5}}
        transition={{ duration: 0.5 }}
        >
            <h4>Skills</h4>
            <article className="skills">
              <div className="skill">
                <AiFillHtml5 title='HTML'/>
                <DiCss3 title='CSS'/>
                <DiJavascript1 title='JavaScript'/>
                <DiReact title='React'/>
                <DiGit title='Git'/>
                <DiPython title='Python'/>
              </div>
              <h5>Responsive design</h5>
              <h5>RESTful Apis</h5>
            </article>
        </motion.article>
    </section>
  )
}

export default About