import React from 'react'
import Resume from '../Images/Resume.pdf' 

const Contact = () => {
  return (
    <section className="contact" id='contact'>
      <div className="text">
        <h3>Contact Me</h3>
        <p><a href="mailto:shashanknathe@gmail.com" target={'_blank'}>shashanknathe@gmail.com</a></p>
        <p>+918483851903</p>
        <a href={Resume} target={'_blank'}>
        <button>Resume</button>
        </a>
      </div>
      <div className="form">
        <form action="https://formsubmit.co/9d5b7bc66ee3d14d3ff6a536cc8f56b2"
        method="POST">

<input type="hidden" name="_captcha" value="false" />
        <input
          type="hidden"
          name="_next"
          value="https://ShashankNathe.github.io/Portfolio/#/thankyou"
        />
        <input type="text" name="name" placeholder="Name" required />
        <input type="email" name="email" placeholder="Email" required />
        <textarea
          name="message"
          placeholder="Enter your message"
          required
        ></textarea>
        <button type="submit">Send</button>
        </form>
      </div>
    </section>
  )
}

export default Contact